import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { createPrediction } from '../actions/predictionActions';
import { PREDICTION_RESET } from '../constants/predictionConstants';
import { useDispatch, useSelector } from 'react-redux';
import {
	Row,
	Col,
	Image,
	ListGroup,
	Button,
	Card,
	Form,
} from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Message from '../components/Message';
import Loader from '../components/Loader';
import FormContainer from '../components/FormContainer';

const HomeScreen = () => {
	const predictionCreate = useSelector((state) => state.predictionCreate);
	const { loading, error, success, prediction } = predictionCreate;
	console.log(error);

	const dispatch = useDispatch();

	const [bookLength, setBookLength] = useState('');
	const [bookLengthAvg, setBookLengthAvg] = useState('');
	const [price, setPrice] = useState('');
	const [priceAvg, setPriceAvg] = useState('');
	const [review, setReview] = useState('');
	const [minListened, setMinListened] = useState('');
	const [completion, setCompletion] = useState('');
	const [supportRequests, setSupportRequests] = useState('');
	const [lastVisited, setLastVisited] = useState('');
	const [purchaseDate, setPurchaseDate] = useState('');
	// const [target, setTarget] = useState('');

	useEffect(() => {
		if (success) {
			setBookLength('');
			setBookLengthAvg('');
			setPrice('');
			setPriceAvg('');
			setReview('');
			setMinListened('');
			setCompletion('');
			setSupportRequests('');
			setLastVisited('');
			setPurchaseDate('');
			// dispatch({ type: PREDICTION_RESET });
		}
	}, [dispatch, success]);
	const submitHandler = (e) => {
		e.preventDefault();

		dispatch(
			createPrediction({
				bookLength,
				bookLengthAvg,
				price,
				priceAvg,
				review,
				minListened,
				completion,
				supportRequests,
				lastVisited,
				purchaseDate,
			})
		);
	};

	return (
		<div>
			<h1>Predict Customer Conversion (Updated)</h1>
			<FormContainer>
				<ListGroup variant='flush'>
					<ListGroup.Item>
						<h4>Will customer convert?</h4>

						<Form onSubmit={submitHandler}>
							<Form.Group controlId='total-booklength'>
								<Form.Label>Total Book Length</Form.Label>
								<Form.Control
									type='number'
									value={bookLength ? bookLength : ''}
									onChange={(e) => setBookLength(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='avg-booklength'>
								<Form.Label>Book Length Average</Form.Label>
								<Form.Control
									type='number'
									value={bookLengthAvg ? bookLengthAvg : ''}
									onChange={(e) => setBookLengthAvg(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='price'>
								<Form.Label>Total Price</Form.Label>
								<Form.Control
									type='number'
									value={price ? price : ''}
									onChange={(e) => setPrice(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='averagePrice'>
								<Form.Label>Average Price</Form.Label>
								<Form.Control
									type='number'
									value={priceAvg ? priceAvg : ''}
									onChange={(e) => setPriceAvg(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='review'>
								<Form.Label>Review</Form.Label>
								<Form.Control
									type='number'
									value={review ? review : ''}
									onChange={(e) => setReview(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='minListened'>
								<Form.Label>Minutes Listened</Form.Label>
								<Form.Control
									type='number'
									value={minListened ? minListened : ''}
									onChange={(e) => setMinListened(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='completion'>
								<Form.Label>Completion</Form.Label>
								<Form.Control
									type='number'
									value={completion ? completion : ''}
									onChange={(e) => setCompletion(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='suppportRequests'>
								<Form.Label>Support Requests</Form.Label>
								<Form.Control
									type='number'
									value={supportRequests ? supportRequests : ''}
									onChange={(e) => setSupportRequests(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='lastVisited'>
								<Form.Label>Last Visited</Form.Label>
								<Form.Control
									type='number'
									value={lastVisited ? lastVisited : ''}
									onChange={(e) => setLastVisited(e.target.value)}
								></Form.Control>
							</Form.Group>
							<Form.Group controlId='purchasedate'>
								<Form.Label>Purchase Date</Form.Label>
								<Form.Control
									type='number'
									value={purchaseDate ? purchaseDate : ''}
									onChange={(e) => setPurchaseDate(e.target.value)}
								></Form.Control>
							</Form.Group>
							{loading && <Loader />}

							{error && <Message variant='danger'>{error}</Message>}
							{prediction &&
								(prediction.target ? (
									<div>
										<Message variant='success'>
											Yes, Customer will convert.
										</Message>
									</div>
								) : (
									<div>
										<Message variant='danger'>No</Message>
									</div>
								))}

							<Button disabled={loading} type='submit' variant='primary'>
								Submit
							</Button>
						</Form>
					</ListGroup.Item>
				</ListGroup>
			</FormContainer>
		</div>
	);
};

export default HomeScreen;
