import axios from 'axios';
import {
	PREDICTION_REQUEST,
	PREDICTION_SUCCESS,
	PREDICTION_FAIL,
} from '../constants/predictionConstants';

export const createPrediction =
	({
		bookLength,
		bookLengthAvg,
		price,
		priceAvg,
		review,
		minListened,
		completion,
		supportRequests,
		lastVisited,
		purchaseDate,
	}) =>
	async (dispatch) => {
		try {
			dispatch({
				type: PREDICTION_REQUEST,
			});
			const config = {
				headers: {
					'Content-type': 'application/json',
				},
			};

			const { data } = await axios.post('/api/predict/', {
				book_length_tot: bookLength,
				book_length_avg: bookLengthAvg,
				price_tot: price,
				price_avg: priceAvg,
				review: review,
				min_listened: minListened,
				completion: completion,
				support_requests: supportRequests,
				last_visited: lastVisited,
				purchase_data: purchaseDate,
			});

			dispatch({
				type: PREDICTION_SUCCESS,
				payload: data,
			});
			localStorage.setItem('predictedValue', JSON.stringify(data.target));
		} catch (error) {
			dispatch({
				type: PREDICTION_FAIL,
				payload:
					error.response && error.response.data.detail
						? error.response.data.detail
						: error.message,
			});
		}
	};
